@import "../../assets/scss/app.scss";

.secondPage {
  padding: 18px 20px;
  height: 100vh;
  box-sizing: border-box;
  background: #B6FBFF;

  @include desktop() {
    padding: 16px 24px 24px;
    background-image: url(../../assets/png/second-back.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      display: flex;
      justify-content: space-between;
      align-items: center;

      img {
        width: 41px;
        height: 48px;
      }

      p {
        @include HessGothic();
        @include onlyDesktop() {
          margin-left: 12px;
          font-size: 26px;
          line-height: 28px;
        }

      }
    }

    .btn {
      cursor: pointer;
      border: none;
      background: #FFF;
      padding: 16px 40px;
      border-radius: 100px;

      @include CenturyGothicBold();
      font-size: 16px;
      line-height: 24px;

    }


  }


}